@use "../utilities/variables" as *;

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $mainColor;
  color: white;

  .logo {
    width: 100px;
    height: 60px;
  }

  .navbar-items {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin-left: auto;

    li {
      list-style: none;
    }

    a {
      color: $mainColor;
      text-decoration: none;
    }
  }
}
