.log-btn {
  background-color: black;
  color: red;
  padding: 10px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  color: white;
}
.navbar .logo {
  width: 100px;
  height: 60px;
}
.navbar .navbar-items {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin-left: auto;
}
.navbar .navbar-items li {
  list-style: none;
}
.navbar .navbar-items a {
  color: black;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 2.5rem;
  background-color: black;
  height: 100vh;
  width: 100vw;
}